.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
  color: #e5e5e5;
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
  background-position: center center;
}

.stars__star:nth-child(-n + 4) {
  background-image: url(images/star-active.svg);
}
